@import '../utilities/index';

* {
  font-family: 'Lato', sans-serif;
  color: $text-color;
  box-sizing: border-box;
}
  
p {
  line-height: 1.45em;
}

a {
  text-decoration: none;
}

.headline-primary {
  color: $primary-color;
  margin-left: $space;
}
  
.headline-secondary {
  font-weight: 300;
  color: $text-color;
}
  
.link {
  color: $secondary-color;
  font-weight: 700;
}
  
.blockquote {
  font-size: 1.5em;
  font-weight: 300;
  font-style: italic;
  margin-left: 20px;
  padding-left: 20px;
  border-left: 8px solid $primary-color;
  border-radius: 5px;
}
  